.menu {
  height: 100vh;
  overflow-y: scroll;
  width: 100vw;
  background-position: center;
  background-size: cover;
}

.menuHeader {
  display: block;
  padding: 1rem;
  text-align: center;
}

.menuText {
  font-family: "Cinzel", serif;
  font-size: 3rem;
  color: rgb(255, 255, 255);
  display: block;
  letter-spacing: 1rem;
}

.menuTagLine {
  font-family: "Dancing Script", cursive;
  font-weight: 600;
  color: white;
  font-size: 1.5rem;
}

.menuHeader span {
  display: block;
}

.menusContainer{
  display: block;
  margin: 2rem;
}

.menuPageContainer{
  width: 100%;
  text-align: center;
}

.menuPage{
  height: auto;
  width: 65vw;
  margin-bottom: 2rem;
}

@media screen and (max-width: 900px) {
  .menuPage {
    width: 90vw;
  }

  .menusContainer{
    margin: 0;
  }
}