.reserveFrontContainer{
  height: 40rem;
  width: 30vw;
  text-align: center;
}

.offersContainer{
  height: inherit;
  padding-top: 4rem;
  
  background-color: #4b4b4b;
}

.carousel-item{
   height: 37rem;
  width: inherit;
  text-align: center;
  border: 0.5px solid white;
  background-color: rgb(133, 133, 133);
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  border-radius: 10px;
}

.carousel-row{
  height: 100%;
  width: 100%;
}


.reserveFront{
  max-width: 90%;
  max-height: 90%;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
